@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&family=Quicksand:wght@400;500&display=swap');
*{
    font-family: 'Quicksand',system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    scroll-behavior: smooth;
}
.playfair-fs{
    font-family: 'Playfair Display', serif;
}
.trace{
    border: 2px solid red;
}
@keyframes FadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @-moz-keyframes FadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @-webkit-keyframes FadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @-o-keyframes FadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @-ms-keyframes FadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
.nav-home::before{
    content: '';    
    height: 100%;
    width: 100%;
    z-index: -1;
    position: absolute;
    background-image: url('https://media.discordapp.net/attachments/1066359032761290813/1091278546099519538/Ferrari_Roma.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    filter: brightness(20%);
    animation-name: FadeIn;
    animation-duration: 3s;
    transition-timing-function: linear;

}
body{
    background-color: black;
}
.text-primary{
    color: #ba160c;
    font-weight: bold;
}